/**
* 疾病风险模块
*
*/
@import "exomeBase";
@import "services/popup";
@import "dist/span";

//图标 文字 横向 的和li
.img-text-horizontal{
	display: block; overflow: hidden; border-radius: 25px; margin-top: 3%; padding: 10px 5%;
	@at-root .ith-box{padding: 0px 3%;}
	&:first-child{background-color: #7bdfd4;}
	&:nth-child(2){background-color: #56dfd0;}
	&:nth-child(3){background-color: #32cbc7;}
	&:nth-child(4){background-color: #3aac9f;}
	&:nth-child(5){background-color: #0b7c70;}
	img{width: 40%;vertical-align: middle;margin-right: 10%;}
	span{@include media-font-size();color: #fff;}
}
//图标 文字 横向 的和li 2列内容
.img-text-horizontal2{
	display: block; overflow: hidden; border-radius: 10px; margin-top: 3%; padding: 10px 1%; border: 1px solid #e1e1e1; width: 48.5%; float: left; text-align: center;
	@at-root .ith2-box{padding: 0px 3%;overflow: hidden;}
	&:nth-child(2n+1){margin-right: 3%;}
	img{width: 45%; vertical-align: middle; margin-right: 3%; border-radius: 50%;}
	span{@include media-font-size();display: inline-block;min-width: 43%;}
}

.grade{
	display: none;padding: 3%;
	h1{border-bottom: 1px solid #dfdfdf;@include media-line-size-height();padding: 10px 0px; box-sizing: content-box; text-align: center; font-weight: bold;}
	p{
		padding: 10px 0px;
		span{
			&.black{@extend %span.black;font-weight: bold;}
			&.red{@extend %span.red;}
			&.block{@extend %span.block;}
			&#judge{padding: 10px;}
		}
	}

}