.popup-box{
	display: none;
	.popup-shade{
		z-index: 19891045; background-color: #000; opacity: 0.3; filter: alpha(opacity=30); top: 0; left: 0; width: 100%; height: 100%; _height: expression(document.body.offsetHeight+"px"); position: fixed; _position: absolute; pointer-events: auto;min-width: 300px; max-width: 640px; right: 0px; margin: 0 auto;
	}
	@at-root .popup-content-box{
		position: fixed;z-index: 19891046; top: 10%; left: 0px; right: 0px; margin: 0 auto; border-radius: 10px;max-width: 600px; 
		.popup-content{   
			background-color: #fff;
		}
	}
}
//展示一个页面并且关闭按钮在底部
.pageBottom{
	@extend .popup-content-box;height: 80%;width: 80%;overflow: hidden;
	.popup-content{   
		height: calc(100% - 45px);overflow-y: auto;
	}
	.popup-close{    
		position: absolute; bottom: 0px; background-color: #b71b2c; height: 45px; width: 100%;text-align: center;
		i.icon-fd-white-close{    
			background: url(../img/icon/icon-fd-white-close.png) center center no-repeat; background-size: contain; display: inline-block; width: 24px; height: 24px; margin-top: 10px;
		}
	}
}
//展示一块内容并且关闭按钮在右上方
.contentRighttop{
	@extend .popup-content-box;width: 75%;
	.popup-content{   
		border-radius: 10px;
	}  
	i.icon-fd-white-close{   
		position: absolute; top: -10px; background-color: #d60e0e; text-align: center; display: inline-block; width: 30px; height: 30px; right: -7px; background-image: url(../img/icon/icon-fd-white-close-no-border.png); border-radius: 50%; background-repeat: no-repeat; background-position: center center; background-size: 60% 60%; 
	}
}